import React, { useState, useEffect, useRef } from 'react';
import WaveSurfer from 'wavesurfer.js';
import {
    IconButton,
    Grid,
} from '@mui/material';
import PauseCircleOutlineRoundedIcon from '@mui/icons-material/PauseCircleOutlineRounded';
import PlayCircleRoundedIcon from '@mui/icons-material/PlayCircleRounded';


const Waveform = ({ audio }) => {
  const containerRef = useRef();
  const waveSurferRef = useRef({
    isPlaying: () => false,
  });
  const [isPlaying, toggleIsPlaying] = useState(false);

  useEffect(() => {
    const waveSurfer = WaveSurfer.create({
      container: containerRef.current,
      fillParent: false,
      height: 50,
      width: 200,
      barWidth: 2,
      barHeight: 2,
      cursorWidth: 1,
    });
    waveSurfer.loadBlob(audio);
    waveSurfer.on('ready', () => {
      waveSurferRef.current = waveSurfer;
      waveSurfer.seekTo(1);
    });

    return () => {
      waveSurfer.destroy();
    };
  }, [audio]);

  return (
    <Grid container>
<div style={{display: "flex", alignItems: "center", flexDirection: "row"}}>
    <IconButton
        onClick={() => {
          waveSurferRef.current.playPause();
          toggleIsPlaying(waveSurferRef.current.isPlaying());
          waveSurferRef.current.on("finish", ()=> {toggleIsPlaying(waveSurferRef.current.isPlaying());});
        }}
        color="primary"
      >
        {isPlaying ? <PauseCircleOutlineRoundedIcon/> : <PlayCircleRoundedIcon/>}
      </IconButton>
    <div style={{width: "100%"}} ref={containerRef}></div>
    </div>
    </Grid>
  );
};


export default Waveform;
