import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import usePromise from 'react-promise';
import { DataGrid, GridToolbar, GridToolbarQuickFilter } from '@mui/x-data-grid';
import Waveform from './waveform';
import CustomNoRowsOverlay from './CustomNoRowsOverlay';
import TrendingDownRoundedIcon from '@mui/icons-material/TrendingDownRounded';
import TrendingUpRoundedIcon from '@mui/icons-material/TrendingUpRounded';
import Chip from '@mui/material/Chip';





const FloatRightGridToolbar = (props) => {
	return(
		<div style={{padding:"0 2em 0 2em"}}>
		<div style={{float: 'right', display: 'flex', flexDirection: 'row', justifyContent: 'space-between', width: '100%'}}>
			<GridToolbarQuickFilter /> <GridToolbar {...props}/>
		</div>
		</div>
		);
}

export default function DataTable({rows, progress, onSelectionModelChange}) {

const columns = [
  {
    field: 'id',
    headerName: 'ID',
    flex: 1,
    hide: true
  },
  { 
    headerName: 'Parent Folder',
    sortable: true,
    flex: 1,
    valueGetter: (params) => params.row.relativePath.split('/').slice(-2, -1) || ''
  },
  { 
    field: 'fileName', 
    headerName: 'FileName',
    sortable: true,
    flex: 1,
  },
  {
    field: 'relativePath',
    headerName: 'Path',
    sortable: true,
    flex: 1,
    hide: true
  },
  {
    field: 'type',
    headerName: 'Type',
    flex: 1,
  },
  {
    field: 'duration',
    headerName: 'Duration',
    flex: 1,
    sortable: true,
    type: 'number',
    valueGetter: (params) => params.row.metadata.duration,
    valueFormatter: (params) => {
              if (params.value == null) {
                return 'loading...';
              }
              return `${params.value.toFixed(2)}s`;
            },
    renderCell: (params) => {
      if (params.row.metadata.duration == null) {
                return <CircularProgress/>;
              }
      return `${params.row.metadata.duration.toFixed(2)}s`;
    }
  },
  {
    field: 'waveform',
    headerName: 'Waveform',
    flex: 1,
    sortable: false,
    renderCell: (params) => (
      <Waveform audio={params.row.file}/>
      )
  },
  {
    field: 'difference',
    headerName: 'Difference',
    flex: 1,
    valueGetter: (params) => {if (params.row.processed?.loaded==true) return params.row.processed?.result?.metadata.duration-params.row.metadata.duration},
    renderCell: (params) => {
        let difference = (params.row.processed?.result?.metadata.duration-params.row.metadata.duration);
        if (!!difference){
        const Icon = () => {if (difference>0) return (<TrendingUpRoundedIcon/>); else return(<TrendingDownRoundedIcon/>)};
       return(
        <Chip sx={{paddingLeft: "10px"}} icon={<Icon style={{paddingLeft:'2px'}}/>} variant="outlined" color={(difference>0)?"error":"success"} label={difference.toFixed(2)+"s"} />
        )
     }
      }
  },
  {
    field: 'processedDuration',
    headerName: 'Pr. Duration',
    flex: 1,
    sortable: true,
    type: 'number',
    valueGetter: (params) => params.row.processed?.result?.metadata.duration||null,
    valueFormatter: (params) => {
      if (!!params.row?.processed) {
              if (params.value == null) {
                return 'loading...';
              }
              return `${params.value.toFixed(2)}s`;
            }
            },
    renderCell: (params) => {
      if (!!params.row.processed) {
        if (!params.row.processed.loaded) {
          if (params.row.processed.progress)
        return (
          <CircularProgress variant="determinate" value={progress}/>
        ); else 
      return (
          <CircularProgress />
        );
    }
        return(
          `${params.row.processed?.result.metadata.duration.toFixed(2)}s`
        )
    }
    }
  },
  {
    field: 'processedFile',
    headerName: 'Processed',
    flex: 1,
    sortable: false,
    renderCell: (params) => {
      if (!!params.row.processed) {
        if (!params.row.processed.loaded) {
          if (params.row.processed.progress)
        return (
          <CircularProgress variant="determinate" value={progress}/>
        ); else 
      return (
          <CircularProgress />
        );
    }
        return(
          <div>
            <Waveform audio={params.row.processed.result.file}/>
          </div>
        )
    }
  }
  },

];

  return (
  	<Box sx={{ width: '100%' }}>
  	<DataGrid
  		components={{ Toolbar: FloatRightGridToolbar, }}
  		autoHeight
        rows={rows}
        columns={columns}
        pageSize={100}
        rowsPerPageOptions={[5]}
        checkboxSelection
        disableSelectionOnClick
        onSelectionModelChange={onSelectionModelChange}
      />
      </Box>
  	)
}
