import React, {
    useState,
    useEffect,
    useMemo
} from 'react';
import {
    createFFmpeg,
    fetchFile
} from '@ffmpeg/ffmpeg';
import './App.css';
import {
    Input,
    Button,
    CardMedia,
    Grid,
    LinearProgress,
    Alert,
    Badge,
    TextField
} from '@mui/material';
import DataTable from './DataTable';
import RadioButtonGroup from './RadioButtonGroup';
import LocalForage from 'localforage';
import { fs, vol } from 'memfs';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';




function App() {
  // debugger
    const [videoSrc, setVideoSrc] = useState('');
    const [progress, setProgress] = useState(0);
    const [totalProgress, setTotalProgress] = useState(0);
    const processedFolder = '/processed';
  //   useEffect(() => {
  //   console.log(`Progress is ${progress}`);
  // });p
    const [files, setFiles] = useState({rows: []});
    const [selectedFiles, setSelectedFiles] = useState({rows: []});
    if (!!files && !files.rows?.length) LocalForage.getItem('initial-data').then(value=>{if (!!value) setFiles(value)});
    const setResolvedFiles = (files) => {
      files = remakeRows({...files});
      setFiles(files);
      LocalForage.setItem('initial-data', files);
    }
    const processRawData = (inputFiles) => {
      let newFiles = makeFilesObject(inputFiles);
        setFiles(newFiles);
        console.log(newFiles);
        Promise.all(newFiles.rows.map(row => row.metadata)).then(resolvedRowsMetadata =>
          {
            resolvedRowsMetadata.forEach(resolvedRowMetadata => {
              newFiles[resolvedRowMetadata.fileId].metadata = resolvedRowMetadata;
            });
            setResolvedFiles(newFiles);
            console.log("promise resolved",newFiles);
          });
        // Promise.all(newFiles.map(f => f.metadata))
        //     .then(resolvedFiles => {
        //       let newResolvedRows = makeResolvedRows(resolvedFiles
        //                     .map(rf => {
        //                         rf.metadata = {
        //                             data: rf.data,
        //                             duration: rf.duration
        //                         };
        //                         return rf;
        //                     }));
        //         setRows(
        //               newResolvedRows
        //         );
        //         console.log("resolved rows seted", newResolvedRows);
        //         LocalForage.setItem('initial-data', newResolvedRows);
        //     });
    }
    const handleFileInputChange = (e) => {
        processRawData(Array.from(e.target.files));
    };
    const [message, setMessage] = useState('Select file & click start');
    const ffmpeg = createFFmpeg({
        log: false,
        logger: ({
            type,
            message
        }) => {
            // if (message !== "use ffmpeg.wasm v0.10.0") console.log(type, message)
        }
    });
    const processSelected = async() => {
      let allFiles = {...files};
      let requestedFiles = {...selectedFiles};
      
      //make a message
      for (let i = 0; i < requestedFiles.rows.length; )
      {
        allFiles[requestedFiles.rows[i].id].processed = {loaded: false, progress: 0};
        i++;
      }
      //notify about started processing
      setFiles( {...allFiles} );

      //make processing
      for (let i = 0; i < requestedFiles.rows.length; )
      {
        allFiles[requestedFiles.rows[i].id].processed = {loaded: false, progress: true};
        // setProgress(0);
        setFiles ( {...allFiles} );
        let result = await processMedia(requestedFiles.rows[i]);
        allFiles[requestedFiles.rows[i].id].processed = {result: result, loaded: true};
        setFiles ( {...allFiles} );
        setTotalProgress((i+1)/requestedFiles.rows.length*100);
        i++;
      }

      //notify about started processing
      setTotalProgress(0);
      setResolvedFiles( {...allFiles} );
      console.log(allFiles);

    }
    let creadtedPath = [];

    const downloadAllProcessedFiles = () => {
        const zip = new JSZip();
        files.rows.forEach((f) => {
            const file = f.processed?.result?.file || (()=>{return})
            zip.file(file.name, file);
        });

        zip.generateAsync({
            type: "blob"
        }).then(function(content) {
            // see FileSaver.js
            saveAs(content, "example.zip");
        });
    }


    const processMedia = async(row) => {
        let selectedMedia = row;
        console.log('Loading ffmpeg-core.js');
        if (!ffmpeg.isLoaded()) {
          await ffmpeg.load();
          ffmpeg.FS("mkdir", 'processed');
        };

        let rememberRatio = 0;
        setProgress(rememberRatio*100);
        ffmpeg.setProgress(({
            ratio
        }) => {
            if (ratio>rememberRatio) {rememberRatio = ratio; setProgress(rememberRatio*100);  console.log(rememberRatio)};
        });
        
        console.log('Start transcoding');

        const formatPath = (string) => {
          return {innerPath: encodeURIComponent(string), outerPath: string}
        }

        const input = formatPath(selectedMedia.id);
        const output = formatPath(`${processedFolder}/${selectedMedia.id.replace(/\.[0-9a-z]+$/,fileExtension)}`);
        console.log(input.innerPath);
        ffmpeg.FS('writeFile', input.innerPath, await fetchFile(selectedMedia.file));

        const args = `-i ${input.innerPath} ${mediaArguments} ${freeform} ${output.innerPath}`;
        await ffmpeg.run(...args.split(' '));

        const data = await ffmpeg.FS('readFile', output.innerPath);
        let file = new File([data.buffer], output.outerPath, {type: selectedMedia.file.type});
        let metadata = await loadMetadata(file, row.id);
        selectedMedia = {...selectedMedia};

        return {file, metadata};
    };
    const loadMetadata = (file, id) => new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
            let audio = new Audio(reader.result);
            audio.preload = "metadata";
            audio.onloadedmetadata = () => {
                resolve({
                    data: reader.result,
                    duration: audio.duration,
                    fileId: id
                })
            };
        }
        reader.onerror = error => reject(error);
    });
    const makeRows = (inputFiles) => {
        return inputFiles.filter(e => {
            if (e.name[0] !== "." && e instanceof Blob) return e
        }).map((e) =>  {
            return  {
                  file: e,
                  id: e.webkitRelativePath,
                  fileName: e.name,
                  relativePath: e.webkitRelativePath,
                  type: e.type,
                  metadata: loadMetadata(e, e.webkitRelativePath)
            }
        });
    }
    const makeFilesObject = (inputFiles) => {
      let rows = makeRows(inputFiles);
      let obj = {};
      rows.forEach(r=>{
        obj[r.id] = r
      });
      obj.rows = Object.values(obj);
      return obj;
    }
    const remakeRows = (files) => {
      delete files.rows;
      files.rows = Object.values(files);
      return files
    }


    const UploadFiles = () => {
      return(
        
        <Grid container justifyContent="center" alignItems="center"
         height={'100%'}
        >
        <Badge badgeContent={files.rows.length} color="primary" max={9999}>
      <Button variant={files.rows.length?"outlined":"contained"} component="label">
      {files.rows.length?'Reupload folder':'Upload folder'}
        
        <input
        type = "file"
        onChange = {handleFileInputChange}
        webkitdirectory = ""
        directory
        multiple
        hidden
          />
        </Button>
        </Badge>

        <Button onClick={processSelected}>Encode Selected</Button>
        </Grid>
        );
    };

    const handleSelectionModelChange = (selectionModel, details) => {
      console.log(selectionModel, details);
      let selection = {};
      selectionModel.forEach(selectedRow=>{
        selection[selectedRow] = files[selectedRow]
      });
      selection.rows = Object.values(selection);
      setSelectedFiles(selection);
    }

    const [freeform, setFreeform] = useState('');
    const handleFreeformChange = (event) => {
      setFreeform(event.target.value);
    }

    

  // const mapPresetsToString = () => {
  //   return presets.map(preset=>preset.options[parseInt(preset.selected)].value).join(" ");
  // }

const [mediaArguments, setMediaArguments] = useState('');
const [fileExtension, setFileExtension] = useState('');

const [presets, setPresets] = useState([{
  type: "argument",
  options: [ 
    {value: `-af silenceremove=1:0:-50dB:0:0:-1:0:-50dB`, label: `yes`},
    {value: ``, label: `no`}
  ],
  label: "Remove silence",
  selected: '0'
},
{
  type: "argument",
  options: [...Array(10).keys()].map(e=>
    {return {value: `-q:a ${e}`, label: `-q:a ${e}`}}),
  label: "Audio quality",
  selected: '0'
},
{
  type: "extension",
  options: [ 
    {value: `.mp3`, label: `mp3`},
    {value: `.wav`, label: `wav`},
    {value: `.ogg`, label: `ogg`}
  ],
  label: "Output format",
  selected: '0'
}
]);

useEffect(()=>{
  setMediaArguments([...presets].filter(e=>{if (e.type=='argument') return e}).map(preset=>preset.options[parseInt(preset.selected)].value).join(" "));
  setFileExtension([...presets].filter(e=>{if (e.type=='extension') return e}).map(e=>e.options[parseInt(e.selected)])[0].value);
},[presets]);

    const Presets  = () => {
    
    const handleChange = (e, id) => {
      const update  = [...presets];
      update[id].selected = e.target.value;
      setPresets(update);
    };

    return (
      <Grid item>
      {presets.map(({options, label, selected}, id)=>{
        return <Grid item><RadioButtonGroup options={options} label={label} changeHandler={function(e){handleChange(e, id)}} selected={selected} /></Grid>;
      })}
      </Grid>
    );

    };

    const MemoizedPresets = useMemo(()=>Presets, [presets]);

  return (
<div className="App">
  <Grid
    container
    direction="column"
    alignItems="center"
    spacing={2}
    >
      <MemoizedPresets/>
      <Grid item>
      <TextField onChange={handleFreeformChange} id="standard-basic" label="your custom input" variant="standard" />
      </Grid>
      <Grid item>
      <UploadFiles/>
      <Button onClick={downloadAllProcessedFiles}>Download all processed files</Button>
      </Grid>
      <Grid item sx={{width: '100%'}}>
      {!!totalProgress&&<LinearProgress variant="determinate" value={totalProgress}/>}
      <DataTable rows={files.rows} progress={progress} onSelectionModelChange={handleSelectionModelChange}/>
    </Grid>
  </Grid>
</div>
  );
}

export default App;
